/* global document, Office */
/* eslint-disable */

const msalConfig = {
  auth: {
    clientId: '4d2f83ea-8f62-4b37-9f6e-5d1d9b16a87e',  // Your Client ID
    authority: 'https://login.microsoftonline.com/common',  // The authority URL
    redirectUri: 'https://outlookaddin.worksorted.com/taskpane.html'  // The redirect URL for OAuth
  },
  cache: {
    cacheLocation: 'localStorage',  // Or 'localStorage' based on your preference
    storeAuthStateInCookie: true,  // Set to true for better compatibility in older browsers
  }
};

const msalInstance = new msal.PublicClientApplication(msalConfig);
Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    const storedUniqueId = localStorage.getItem('outlookuniqueId');

    if (storedUniqueId) {
      const accounts = msalInstance.getAllAccounts();
      const matchedAccount = accounts.find(account => account.idTokenClaims.oid === storedUniqueId);
      if (matchedAccount) {
        msalInstance.setActiveAccount(matchedAccount);
        window.location.href = "crm.html";
      } else {
        // Clear storage if IDs do not match
        localStorage.removeItem('outlookuniqueId');
      }
    } else {
      // No active account, set up login button
      document.getElementById("loginBtn").onclick = loginAndFetchUserInfo;
    }
  }
});

function loginAndFetchUserInfo() {
  const loginRequest = {
    scopes: ['user.read', 'mail.read']  // Scopes for Microsoft Graph API
  };

  // Login and set active account
  msalInstance.loginPopup(loginRequest)
    .then(function (loginResponse) {
      console.log("Login Success:", loginResponse);

      // Set the active account after login
      msalInstance.setActiveAccount(loginResponse.account);

      // Attempt to silently acquire the token
      acquireToken();
    })
    .catch(function (error) {
      console.error("Error during login:", error);
    });
}

function acquireToken() {
  const tokenRequest = {
    scopes: ['user.read', 'mail.read'],
    account: msalInstance.getActiveAccount()  // Use the active account for token acquisition
  };

  msalInstance.acquireTokenSilent(tokenRequest)
    .then(function (tokenResponse) {
      const accessToken = tokenResponse.uniqueId;
      console.log("Access Token:", tokenResponse);

      // Use the token to fetch user data from Microsoft Graph
      //fetchUserData(accessToken);
      window.location.href = 'https://worksorted.com/login.php?outlookuniqueId=' + tokenResponse.uniqueId;

      const checkLoginStatus = setInterval(async function () {
        try {
          const response = await fetch("https://worksorted.com/outlookAddin/check_login.php", {
            method: "POST",
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            },
            body: `access_token=${accessToken}`
          });
          const result = await response.json();
          console.log("Result from server:", result);
          if (result.status === "success") {
            console.log("User login！");
            localStorage.setItem('outlookuniqueId', tokenResponse.uniqueId);
            // stop checking login status
            clearInterval(checkLoginStatus);

            // navigate to CRM page
            window.location.href = "crm.html";
          } else {
            console.log("User not login！");
          }
        } catch (error) {
          console.error("Error:", error);
        }
      }, 2000);
    })
    .catch(function (error) {
      console.error("Error acquiring token silently:", error);
      if (error instanceof msal.InteractionRequiredAuthError) {
        // If silent token acquisition fails, try interactive method
        msalInstance.acquireTokenPopup(tokenRequest)
          .then(function (tokenResponse) {
            const accessToken = tokenResponse.uniqueId;
            window.location.href = 'https://worksorted.com/login.php?outlookuniqueId=' + tokenResponse.uniqueId;
            const checkLoginStatus = setInterval(async function () {
              try {
                const response = await fetch("https://worksorted.com/outlookAddin/check_login.php", {
                  method: "POST",
                  headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                  },
                  body: `access_token=${accessToken}`
                });
                const result = await response.json();
                console.log("Result from server:", result);
                if (result.status === "success") {
                  console.log("User login！");
                  localStorage.setItem('outlookuniqueId', tokenResponse.uniqueId);
                  // stop checking login status
                  clearInterval(checkLoginStatus);

                  // navigate to CRM page
                  window.location.href = "crm.html";
                } else {
                  console.log("User not login！");
                }
              } catch (error) {
                console.error("Error:", error);
              }
            }, 2000);
          })
          .catch(function (popupError) {
            console.error("Error acquiring token with popup:", popupError);
          });
      }
    });
}

function fetchUserData(accessToken) {
  fetch('https://graph.microsoft.com/v1.0/me', {
    method: 'GET',
    headers: {
      'Authorization': 'Bearer ' + accessToken,
      'Content-Type': 'application/json'
    }
  })
    .then(response => response.json())
    .then(data => {
      console.log("User Data:", data);
    })
    .catch(error => {
      console.error('Error fetching user data from Microsoft Graph:', error);
    });
}

